/* header */

header {
    background-color: #3d3d3d;
    border-bottom: 1px solid #d2ac67;
}

.navigation {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: #3d3d3d;
    font-family: sans-serif;
    color: white;
    text-align: center;
    padding-bottom: 10px;
}

.navigation .hamburger {
    float: left;
}

.navigation .tel {
    font-size: 1.3rem;
    color: #ffffff;
    margin-right: 10px;
}

.navigation .tel-block {
    float: right;
    border: 1px solid #d2ac67;
    padding: 5px;
    border-radius: 5px;
}

.navigation .tel-block img {
    margin-right: 10px;
}

.navigation-toggle {
    display: inline-block;
    padding: 10px 0 10px 20px;
}

.navigation-toggle-btn {
    display: none;
}

nav {
    display: none;
}

.navigation-toggle-btn:checked ~ nav {
    display: block;
}

.navigation-toggle-btn:checked ~ .navigation__items--right {
    display: none;
}


.icon-bar {
    display: block;
    width: 22px;
    height: 2px;
    background-color: #fff;
    margin: 4px 0;
    padding: 0;
    transition: all 0.2s;
}

.navigation-toggle-btn:checked ~ label > .hamburger > .top-bar {
    transform: rotate(45deg);
    transform-origin: 10% 10%;
}

.navigation-toggle-btn:checked ~ label > .hamburger > .middle-bar {
    opacity: 0;
}

.navigation-toggle-btn:checked ~ label > .hamburger > .bottom-bar {
    transform: rotate(-45deg);
    transform-origin: 10% 90%;
}

.navigation__items {
    margin-top: 5px;
    padding: 0 10px;
    order: 2;
}

.navigation__items ul {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;
}

.navigation__items li {
    padding: 10px;
    list-style-type: none;
    color: #ffff;
}

.navigation__items li a {
    color: #ffff;
    display: block;
    padding: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    font-size: 13px;
    border: 1px solid #d2ac67;
}

.navigation__items--right {
    display: none;
}

.navigation__items--right ul li a {
    font-size: 40px;
}

.navigation__items--right ul li a:hover {
    text-decoration: none;
}


@media only screen and (min-width: 768px) {

    nav {
        display: block;
    }

    .navigation__items {
        order: 2;
    }

    .navigation, .navigation__items ul {
        flex-direction: row;
        text-align: left;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
    }

    .navigation__items li a {
        font-size: 16px;
        border: none;
    }

    .navigation-toggle {
        display: none;
    }

    .navigation__items--right {
        display: inline-block;
        order: 1;

    }

    .navigation__items--right ul {
        justify-content: flex-start;
    }
}

