    height: 5px;
    background: #46c765;
}

.tabs ul li {
    margin: 0;
    padding: 0;
    cursor: pointer;
    display: block;
    float: left;
    padding: 10px 15px;
    background: #e9eaeb;
    color: #707070;
}

.tabs ul li.active, .tabs ul li.active:hover {
    background: #46c765;
    color: #fff;
}

.tabs ul li:hover {
    background: #d6d6d7;
}


